// export default Jobs;

"use client";

import React, { useState, useEffect } from "react";
import Filters from "./Filters";
import Job from "./Job";

const Jobs = ({ allJobs }) => {
  const [selectedFilters, setSelectedFilters] = useState({
    jobType: [],
    salaryRange: "",
    experienceRange: "",
  });
  const [filteredJobs, setFilteredJobs] = useState(allJobs);

  useEffect(() => {
    filterJobs();
  }, [selectedFilters, allJobs]);

  const filterJobs = () => {
    let filtered = allJobs.filter((job) => {
      const jobTypeMatch =
        selectedFilters.jobType?.length === 0 ||
        selectedFilters.jobType.includes(job.jobType.toLowerCase());

      const salaryMatch = selectedFilters.salaryRange
        ? (() => {
            const [min, max] = selectedFilters.salaryRange
              .split("-")
              .map((val) => parseInt(val.replace("k", "000"), 10));
            const jobSalary = parseInt(job.salary.replace("k", "000"), 10);
            return jobSalary >= min && jobSalary <= max;
          })()
        : true;

      const experienceMatch = selectedFilters.experienceRange
        ? (() => {
            const [min, max] = selectedFilters.experienceRange
              .split("-")
              .map(Number);
            return job.experienceLevel >= min && job.experienceLevel <= max;
          })()
        : true;

      return jobTypeMatch && salaryMatch && experienceMatch;
    });
    setFilteredJobs(filtered);
  };

  return (
    <div>
      <Filters
        selectedFilters={selectedFilters}
        setSelectedFilters={setSelectedFilters}
      />
      <div className="job-list">
        {filteredJobs?.length > 0 ? (
          filteredJobs?.map((job) => <Job key={job._id} job={job} />)
        ) : (
          <p>No jobs found.</p>
        )}
      </div>
    </div>
  );
};

export default Jobs;
